/* Navbar container */
.navbar {
    background-color: #333;
    padding: 10px;
  }
  
  /* Navbar list items */
  .navbar li {
    display: inline;
    margin-right: 20px;
  }
  
  /* Navbar links */
  .navbar a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
  }
  
  /* Navbar links hover effect */
  .navbar a:hover {
    color: #ff4500; /* Change to desired hover color */
  }
  